function onSendEvent(event) {
    sendEvent = event;
    
    if(recipientCount>=threshold){
	//localStorage.setItem("recipients", JSON.stringify(recipientCount));
	Office.context.ui.displayDialogAsync('https://intrach2.novateam.com/outlook/taskpane.html', {height: 40, width: 30},
                                           function (asyncResult) {
        	dialog = asyncResult.value;
		//dialog.messageChild(JSON.stringify(recipientCount));
        	dialog.addEventHandler(Office.EventType.DialogMessageReceived, processMessage);
        	dialog.addEventHandler(Office.EventType.DialogEventReceived, processEvent);
    });
  }else{
      sendEvent.completed({ allowEvent: true });
  }
 
}

function processEvent(args){
      sendEvent.completed({ allowEvent: false});
}

// PROCESS result fro dialog
function processMessage(arg) {
    dialog.close();
    var message = arg.message;
    
if(message==="valid"){
      sendEvent.completed({ allowEvent: true });  
    }
    if(message==="cancel"){
      sendEvent.completed({ allowEvent: false });  
    }
}

function writeToDoc(text){
	Office.context.document.setSelectedDataAsync(text,
		function(ayncResult){
			var error = asyncResult.error;
			if(asyncResult.status==="failed"){
				console.log("error"+asyncResult.error.message);
			}
		}
	)
}

function getGlobal() {
  return typeof self !== "undefined"
    ? self
    : typeof window !== "undefined"
    ? window
    : typeof global !== "undefined"
    ? global
    : undefined;
}


const g = getGlobal();

// The add-in command functions need to be available in global scope
g.onSendEvent = onSendEvent;


// Get the email addresses of all the recipients of the composed item.
function getAllRecipients() {
    // Local objects to point to recipients of either
    // the appointment or message that is being composed.
    // bccRecipients applies to only messages, not appointments.
    var toRecipients, ccRecipients, bccRecipients;
    // Verify if the composed item is an appointment or message.
    if (item.itemType == Office.MailboxEnums.ItemType.Appointment) {
        toRecipients = item.requiredAttendees;
        ccRecipients = item.optionalAttendees;
    }
    else {
        toRecipients = item.to;
        ccRecipients = item.cc;
        bccRecipients = item.bcc;
    }
   
    // Use asynchronous method getAsync to get each type of recipients
    // of the composed item. Each time, this example passes an anonymous
    // callback function that doesn't take any parameters.
    toRecipients.getAsync(function (asyncResult) {
        if (asyncResult.status == Office.AsyncResultStatus.Failed){
            //write(asyncResult.error.message);
        }
        else {
            // Async call to get to-recipients of the item completed.
            // Display the email addresses of the to-recipients.
            //write ('To-recipients of the item:');
            displayAddresses(asyncResult);
        }    
    }); // End getAsync for to-recipients.

    // Get any cc-recipients.
    ccRecipients.getAsync(function (asyncResult) {
        if (asyncResult.status == Office.AsyncResultStatus.Failed){
            //write(asyncResult.error.message);
        }
        else {
            // Async call to get cc-recipients of the item completed.
            // Display the email addresses of the cc-recipients.
            //write ('Cc-recipients of the item:');
            displayAddresses(asyncResult);
        }
    }); // End getAsync for cc-recipients.

    // If the item has the bcc field, i.e., item is message,
    // get any bcc-recipients.
    if (bccRecipients) {
        bccRecipients.getAsync(function (asyncResult) {
        if (asyncResult.status == Office.AsyncResultStatus.Failed){
            //write(asyncResult.error.message);
        }
        else {
            // Async call to get bcc-recipients of the item completed.
            // Display the email addresses of the bcc-recipients.
            //write ('Bcc-recipients of the item:');
            displayAddresses(asyncResult);
        }
                       
        }); // End getAsync for bcc-recipients.
     }
}

// Recipients are in an array of EmailAddressDetails
// objects passed in asyncResult.value.
function displayAddresses (asyncResult) {
    for (var i=0; i<asyncResult.value.length; i++){
        recipientCount+=1;
        //write (asyncResult.value[i].emailAddress);
    }
}

